// =============================================================================
// API Key Validation
// =============================================================================
// Validates NexFlow API keys for authentication
// Supports both live (nf_live_*) and test (nf_test_*) keys

import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'ValidateApiKey' });

/**
 * Validated API key result
 */
export interface ValidatedApiKey {
  /** Unique identifier for the API key (first 20 chars or hash) */
  id: string;
  /** Account ID associated with this key (if known) */
  accountId?: string;
  /** Whether this is a live or test key */
  isLive: boolean;
  /** Whether the key is valid */
  valid: boolean;
}

/**
 * API key prefix patterns
 */
const API_KEY_PREFIXES = {
  LIVE: 'nf_live_',
  TEST: 'nf_test_',
} as const;

/**
 * Validate an API key and return key metadata
 * 
 * @param apiKey - The API key to validate
 * @returns Validated key info or null if invalid
 * 
 * @example
 * ```ts
 * const key = await validateApiKey('nf_live_abc123xyz...');
 * if (key) {
 *   console.log(`Key ${key.id} is valid`);
 * }
 * ```
 */
export async function validateApiKey(apiKey: string): Promise<ValidatedApiKey | null> {
  if (!apiKey || typeof apiKey !== 'string') {
    logger.debug({ reason: 'empty' }, 'API key validation failed');
    return null;
  }

  // Check for valid prefix
  const isLive = apiKey.startsWith(API_KEY_PREFIXES.LIVE);
  const isTest = apiKey.startsWith(API_KEY_PREFIXES.TEST);

  if (!isLive && !isTest) {
    logger.debug({ reason: 'invalid_prefix' }, 'API key validation failed');
    return null;
  }

  // Extract key body (after prefix)
  const prefix = isLive ? API_KEY_PREFIXES.LIVE : API_KEY_PREFIXES.TEST;
  const keyBody = apiKey.slice(prefix.length);

  // Basic validation: key body should be at least 16 characters
  if (keyBody.length < 16) {
    logger.debug({ reason: 'too_short' }, 'API key validation failed');
    return null;
  }

  // Generate a stable ID from the key (first 20 chars of full key)
  const id = apiKey.substring(0, 20);

  // In a production system, you would:
  // 1. Hash the key and look it up in the database
  // 2. Check expiration, rate limits, permissions
  // 3. Return associated account info
  
  // For now, we accept any well-formed key
  // Real validation should be added when accounts system is integrated
  
  logger.debug({ id, isLive }, 'API key validated');

  return {
    id,
    isLive,
    valid: true,
  };
}

/**
 * Check if an API key format is valid (without database lookup)
 * 
 * @param apiKey - The API key to check
 * @returns true if format is valid
 */
export function isValidApiKeyFormat(apiKey: string): boolean {
  if (!apiKey || typeof apiKey !== 'string') return false;
  
  const isLive = apiKey.startsWith(API_KEY_PREFIXES.LIVE);
  const isTest = apiKey.startsWith(API_KEY_PREFIXES.TEST);
  
  if (!isLive && !isTest) return false;
  
  const prefix = isLive ? API_KEY_PREFIXES.LIVE : API_KEY_PREFIXES.TEST;
  const keyBody = apiKey.slice(prefix.length);
  
  return keyBody.length >= 16;
}

/**
 * Extract key type from API key
 */
export function getApiKeyType(apiKey: string): 'live' | 'test' | 'unknown' {
  if (apiKey?.startsWith(API_KEY_PREFIXES.LIVE)) return 'live';
  if (apiKey?.startsWith(API_KEY_PREFIXES.TEST)) return 'test';
  return 'unknown';
}

