// =============================================================================
// SMF Registration API
// =============================================================================
// POST /api/v1/smf/register - Register a new facilitator
// GET /api/v1/smf/register?id={facilitator_id} - Verify DNS / get status

import { NextRequest, NextResponse } from 'next/server';
import { createLogger } from '@/lib/logger';
import { validateApiKey } from '@/lib/auth/validate-api-key';
import { getFacilitatorRegistry } from '@/services/smf/facilitator-registry';
import {
  type FacilitatorRegistrationRequest,
  isValidRegistrationRequest,
} from '@/types/facilitator-registration';

const logger = createLogger({ component: 'SMFRegisterAPI' });

// =============================================================================
// POST /api/v1/smf/register - Register new facilitator
// =============================================================================

export async function POST(req: NextRequest) {
  try {
    // 1. Authenticate via API key
    const authHeader = req.headers.get('authorization');
    if (!authHeader) {
      return NextResponse.json(
        { error: 'MISSING_API_KEY', message: 'Authorization header required' },
        { status: 401 }
      );
    }

    const apiKey = authHeader.replace(/^Bearer\s+/i, '');
    if (!apiKey) {
      return NextResponse.json(
        { error: 'MISSING_API_KEY', message: 'API key required' },
        { status: 401 }
      );
    }

    const validatedKey = await validateApiKey(apiKey);
    if (!validatedKey) {
      return NextResponse.json(
        { error: 'INVALID_API_KEY', message: 'Invalid API key' },
        { status: 401 }
      );
    }

    // 2. Parse request body
    let body: unknown;
    try {
      body = await req.json();
    } catch {
      return NextResponse.json(
        { error: 'INVALID_REQUEST', message: 'Invalid JSON body' },
        { status: 400 }
      );
    }

    // 3. Validate request
    if (!isValidRegistrationRequest(body)) {
      return NextResponse.json(
        {
          error: 'INVALID_REQUEST',
          message: 'Invalid registration request. Required fields: name, domain, dns_txt_proof (starting with "nexflow-verify="), callback_url (HTTPS), supported_chains, supported_tokens',
        },
        { status: 400 }
      );
    }

    const registrationRequest = body as FacilitatorRegistrationRequest;

    // 4. Register facilitator
    const registry = getFacilitatorRegistry();
    const response = await registry.register(registrationRequest, validatedKey.id);

    logger.info({
      facilitator_id: response.facilitator_id,
      domain: registrationRequest.domain,
      registered_by: validatedKey.id,
    }, 'Facilitator registration created');

    return NextResponse.json(response, { status: 201 });
  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : 'Unknown error';

    // Handle specific errors
    if (errorMessage === 'DOMAIN_ALREADY_REGISTERED') {
      return NextResponse.json(
        { error: 'DOMAIN_ALREADY_REGISTERED', message: 'This domain is already registered' },
        { status: 409 }
      );
    }

    logger.error({ error }, 'Registration failed');
    return NextResponse.json(
      { error: 'INTERNAL_ERROR', message: errorMessage },
      { status: 500 }
    );
  }
}

// =============================================================================
// GET /api/v1/smf/register?id={facilitator_id} - Verify DNS / Get status
// =============================================================================

export async function GET(req: NextRequest) {
  try {
    const facilitator_id = req.nextUrl.searchParams.get('id');
    const action = req.nextUrl.searchParams.get('action');

    if (!facilitator_id) {
      return NextResponse.json(
        { error: 'MISSING_ID', message: 'facilitator_id is required' },
        { status: 400 }
      );
    }

    const registry = getFacilitatorRegistry();

    // Action: verify - Trigger DNS verification
    if (action === 'verify') {
      const ipAddress =
        req.headers.get('x-forwarded-for')?.split(',')[0]?.trim() ||
        req.headers.get('x-real-ip') ||
        'unknown';

      try {
        const response = await registry.verifyDNS(facilitator_id, ipAddress);

        const status = response.status === 'verified' ? 200 : 200;
        return NextResponse.json(response, { status });
      } catch (error) {
        const errorMessage = error instanceof Error ? error.message : 'Unknown error';

        if (errorMessage === 'FACILITATOR_NOT_FOUND') {
          return NextResponse.json(
            { error: 'FACILITATOR_NOT_FOUND', message: 'Facilitator not found' },
            { status: 404 }
          );
        }

        logger.error({ error, facilitator_id }, 'DNS verification failed');
        return NextResponse.json(
          { error: 'INTERNAL_ERROR', message: errorMessage },
          { status: 500 }
        );
      }
    }

    // Default: Get status
    try {
      const status = await registry.getFacilitatorStatus(facilitator_id);
      return NextResponse.json(status);
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Unknown error';

      if (errorMessage === 'FACILITATOR_NOT_FOUND') {
        return NextResponse.json(
          { error: 'FACILITATOR_NOT_FOUND', message: 'Facilitator not found' },
          { status: 404 }
        );
      }

      logger.error({ error, facilitator_id }, 'Get status failed');
      return NextResponse.json(
        { error: 'INTERNAL_ERROR', message: errorMessage },
        { status: 500 }
      );
    }
  } catch (error) {
    logger.error({ error }, 'Request failed');
    return NextResponse.json(
      { error: 'INTERNAL_ERROR', message: 'Internal server error' },
      { status: 500 }
    );
  }
}

